import { IgcBaseEventArgsDetail } from "./igc-base-event-args-detail";
import { ColumnMovingEndEventArgsDetail as ColumnMovingEndEventArgsDetail_internal } from "./ColumnMovingEndEventArgsDetail";
import { ensureBool } from "igniteui-core/componentUtil";

/**
 * Represents event arguments related to the end of a column moving operation in a grid
*/
export class IgcColumnMovingEndEventArgsDetail extends IgcBaseEventArgsDetail
{

protected createImplementation() : ColumnMovingEndEventArgsDetail_internal
{
	return new ColumnMovingEndEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : ColumnMovingEndEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	/**
	 * The source of the event represents the column that is being moved.
	 * The `ColumnType` contains the information (the grid it belongs to, css data, settings, etc.) of the column in its properties
	*/
	get source() : IgcColumnComponent {
	                                        const r = this.i.g;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = IgcColumnComponent._createFromInternal(r);
	                                            if (e) {
	                                                (e as any)._implementation = r;
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set source(v: IgcColumnComponent) {
		v == null ? this.i.g = null : this.i.g = v.i;
	}
	/**
	 * The target of the event represents the column, the source is being moved to.
	 * The `ColumnType` contains the information (the grid it belongs to, css data, settings, etc.) of the column in its properties
	*/
	get target() : IgcColumnComponent {
	                                        const r = this.i.h;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = IgcColumnComponent._createFromInternal(r);
	                                            if (e) {
	                                                (e as any)._implementation = r;
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set target(v: IgcColumnComponent) {
		v == null ? this.i.h = null : this.i.h = v.i;
	}
	/**
	 * `cancel` returns whether the event has been intercepted and stopped
	 * If the value becomes "true", it returns/exits from the method, instantiating the interface
	*/
	get cancel() : boolean {
		return (this.i.i as boolean);
	}
	set cancel(v: boolean) {
		this.i.i = ensureBool(v);
	}
	
	    public findByName(name: string): any {
	        
	    var baseResult = super.findByName(name);
	    if (baseResult)
	    {
	        return baseResult;
	    }
	
	if (this.source && (this.source as any).name && (this.source as any).name == name) { return this.source; }
	if (this.target && (this.target as any).name && (this.target as any).name == name) { return this.target; }
	
	        return null;
	    }
}
