import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcRowType } from "./igc-row-type";
import { IgcBaseEventArgsDetail } from "./igc-base-event-args-detail";
import { GridRowEventArgsDetail as GridRowEventArgsDetail_internal } from "./GridRowEventArgsDetail";

/**
 * Represents an event argument related to grid row interactions.
*/
export class IgcGridRowEventArgsDetail extends IgcBaseEventArgsDetail
{

protected createImplementation() : GridRowEventArgsDetail_internal
{
	return new GridRowEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : GridRowEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	/**
	 * Represents the grid row that triggered the event.
	*/
	get row() : IgcRowType {
	                                        const r = this.i.g;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcRowType();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set row(v: IgcRowType) {
		v == null ? this.i.g = null : this.i.g = v.i;
	}
	/**
	 * Represents the original event that occurred
	 * Examples of such events include: selecting, clicking, double clicking, etc.
	*/
	get event() : any {
		return this.i.h as any;
	}
	set event(v: any) {
	                                        this.i.h = v;
	}
	
	    public findByName(name: string): any {
	        
	    var baseResult = super.findByName(name);
	    if (baseResult)
	    {
	        return baseResult;
	    }
	
	if (this.row && (this.row as any).name && (this.row as any).name == name) { return this.row; }
	
	        return null;
	    }
}
