import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { delegateCombine, delegateRemove } from 'igniteui-core/type';
import { IgcPivotAggregator } from "./igc-pivot-aggregator";
import { GridColumnDataType, GridColumnDataType_$type } from "./GridColumnDataType";
import { PivotValue as PivotValue_internal } from "./PivotValue";
import { ensureBool, ensureEnum } from "igniteui-core/componentUtil";

/**
 * Configuration of a pivot value aggregation.
*/
export class IgcPivotValue
{

protected createImplementation() : PivotValue_internal
{
	
let impl = new PivotValue_internal();
if ((impl as any).setNativeElement) {
    (impl as any).setNativeElement({});
}
return impl;

}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : PivotValue_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	/**
	 * Field name to use in order to extract value.
	*/
	get member() : string {
		return (this.i.l as string);
	}
	set member(v: string) {
		this.i.l = v;
	}
	/**
	 * Display name to show instead of member for the column header of this value. *
	*/
	get displayName() : string {
		return (this.i.k as string);
	}
	set displayName(v: string) {
		this.i.k = v;
	}
	/**
	 * Active aggregator definition with key, label and aggregator.
	*/
	get aggregate() : IgcPivotAggregator {
	                                        const r = this.i.c;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcPivotAggregator();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set aggregate(v: IgcPivotAggregator) {
		v == null ? this.i.c = null : this.i.c = v.i;
	}
	/**
	 * List of aggregates to show in aggregate drop-down.
	*/
	get aggregateList() : IgcPivotAggregator[] {
		if (!this.i.a) { return undefined; }
	                                        let ret: IgcPivotAggregator[] = [];
	                                        for (let i = 0; i < this.i.a.length; i++) {
	                                            let impl = this.i.a[i];
	                                            if (!(<any>impl).externalObject) {
	                                                if (impl instanceof IgcPivotAggregator) {
	                                                    ret.push(impl);
	                                                    continue;
	                                                }
	                                                let e = new IgcPivotAggregator();
	                                                (<any>e)._implementation = impl;
	                                                (<any>impl).externalObject = e;
	                                            }
	                                            ret.push((<any>impl).externalObject);
	                                        }
	                                        return ret;
	}
	set aggregateList(v: IgcPivotAggregator[]) {
	                                        let arr: any[] = [];
	                                        for (let i = 0; i < v.length; i++) {
	                                            arr.push(v[i].i);
	                                        }
	                                        this.i.a = arr;
	                                    }
	/**
	 * Enables/Disables a particular value from pivot aggregation.
	*/
	get enabled() : boolean {
		return (this.i.g as boolean);
	}
	set enabled(v: boolean) {
		this.i.g = ensureBool(v);
	}
	/**
	 * Allow conditionally styling of the IgxPivotGrid cells.
	*/
	get styles() : any {
		return this.i.j as any;
	}
	set styles(v: any) {
	                                        this.i.j = v;
	}
	/**
	 * Enables a data type specific template of the cells
	*/
	get dataType() : GridColumnDataType {
		return this.i.b;
	}
	set dataType(v: GridColumnDataType) {
		this.i.b = ensureEnum<GridColumnDataType>(GridColumnDataType_$type, v);
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	if (this.aggregate && (this.aggregate as any).name && (this.aggregate as any).name == name) { return this.aggregate; }
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
