	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	if (this.cellID && (this.cellID as any).name && (this.cellID as any).name == name) { return this.cellID; }
	if (this.column && (this.column as any).name && (this.column as any).name == name) { return this.column; }
	if (this.owner && (this.owner as any).name && (this.owner as any).name == name) { return this.owner; }
	
	        return null;
	    }
