import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcColumnComponent } from "./igc-column-component";
import { IgcBaseEventArgsDetail } from "./igc-base-event-args-detail";
import { ColumnMovingStartEventArgsDetail as ColumnMovingStartEventArgsDetail_internal } from "./ColumnMovingStartEventArgsDetail";

/**
 * Represents event arguments related to the start of a column moving operation in a grid.
*/
export class IgcColumnMovingStartEventArgsDetail extends IgcBaseEventArgsDetail
{

protected createImplementation() : ColumnMovingStartEventArgsDetail_internal
{
	return new ColumnMovingStartEventArgsDetail_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : ColumnMovingStartEventArgsDetail_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	/**
	 * Represents the column that is being moved.
	 * The `ColumnType` contains the information (the grid it belongs to, css data, settings, etc.) of the column in its properties
	*/
	get source() : IgcColumnComponent {
	                                        const r = this.i.g;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = IgcColumnComponent._createFromInternal(r);
	                                            if (e) {
	                                                (e as any)._implementation = r;
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set source(v: IgcColumnComponent) {
		v == null ? this.i.g = null : this.i.g = v.i;
	}
	
	    public findByName(name: string): any {
	        
	    var baseResult = super.findByName(name);
	    if (baseResult)
	    {
	        return baseResult;
	    }
	
	if (this.source && (this.source as any).name && (this.source as any).name == name) { return this.source; }
	
	        return null;
	    }
}
