import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcBaseSearchInfo } from "./igc-base-search-info";
import { SearchInfo as SearchInfo_internal } from "./SearchInfo";

export class IgcSearchInfo extends IgcBaseSearchInfo
{

protected createImplementation() : SearchInfo_internal
{
	
let impl = new SearchInfo_internal();
if ((impl as any).setNativeElement) {
    (impl as any).setNativeElement({});
}
return impl;

}
	/**
	                             * @hidden 
	                             */
	                            public get i() : SearchInfo_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	get matchInfoCache() : any[] {
		return this.i.k as any[];
	}
	set matchInfoCache(v: any[]) {
	                                            if (v && !Array.isArray(v) && typeof(v) == "string") {
	                                                const re = /\s*(?:,|\s|$)\s*/gm;
	                                                v = (v as string).split(re) as any as any[];
	                                            }
		    this.i.k = v;
	    }
	get activeMatchIndex() : number {
		return (this.i.l as number);
	}
	set activeMatchIndex(v: number) {
		this.i.l = +v;
	}
}
