import { Base, Type, markType } from "./type";
import { isNaN_, isInfinity } from "./number";

/**
 * @hidden 
 */
export class MathUtil extends Base {
	static $t: Type = markType(MathUtil, 'MathUtil');
	static readonly n: number = (1 + Math.sqrt(5)) / 2;
	static readonly q: number = Math.sqrt(2);
	static b(a: number): number {
		return Math.log(a + Math.sqrt(a * a + 1));
	}
	static s(number_: number): string {
		return number_.toString();
	}
	static g(a: number, b: number): number {
		return Math.sqrt(a * a + b * b);
	}
	static p(a: number): number {
		return a * a;
	}
	static a(a: number): boolean {
		return !isNaN_(a) && !isInfinity(a);
	}
	static d(a: number, b: number, c: number): number {
		return Math.min(c, Math.max(b, a));
	}
	static o(a: number): number {
		return Math.PI * a / 180;
	}
	static f(a: number): number {
		return 180 * a / Math.PI;
	}
	static l(a: number, b: number, c: number): number {
		return Math.min(a, Math.min(b, c));
	}
	static i(a: number, b: number, c: number): number {
		return Math.max(a, Math.max(b, c));
	}
	static k(...a: number[]): number {
		let b: number = a[0];
		for (let c: number = 1; c < a.length; ++c) {
			b = Math.min(b, a[c]);
		}
		return b;
	}
	static m(a: number[]): number {
		let b: number = a[0];
		for (let c: number = 1; c < a.length; ++c) {
			b = Math.min(b, a[c]);
		}
		return b;
	}
	static h(...a: number[]): number {
		let b: number = a[0];
		for (let c: number = 1; c < a.length; ++c) {
			b = Math.max(b, a[c]);
		}
		return b;
	}
	static j(a: number[]): number {
		let b: number = a[0];
		for (let c: number = 1; c < a.length; ++c) {
			b = Math.max(b, a[c]);
		}
		return b;
	}
	static r(a: number[]): number {
		if (a.length == 0) {
			return 0;
		}
		let b = 0;
		for (let d = 0; d < a.length; d++) {
			let c = a[d];
			if (isNaN_(c)) {
				continue;
			}
			if (isInfinity(c)) {
				continue;
			}
			b += c;
		}
		return b;
	}
	static c(a: number[]): number {
		if (a.length == 0) {
			return 0;
		}
		let b = 0;
		let c = 0;
		for (let e = 0; e < a.length; e++) {
			let d = a[e];
			if (isNaN_(d)) {
				continue;
			}
			if (isInfinity(d)) {
				continue;
			}
			b += d;
			c++;
		}
		return b / c;
	}
}


