import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcColumnComponent } from "./igc-column-component";
import { SortingDirection, SortingDirection_$type } from "./SortingDirection";
import { HeaderType as HeaderType_internal } from "./HeaderType";
import { ensureBool, ensureEnum } from "igniteui-core/componentUtil";

/**
 * Interface representing a header cell in the grid. It is essentially the blueprint to a header cell object.
 * Contains definitions of properties, relevant to the header
*/
export class IgcHeaderType
{

protected createImplementation() : HeaderType_internal
{
	
let impl = new HeaderType_internal();
if ((impl as any).setNativeElement) {
    (impl as any).setNativeElement({});
}
return impl;

}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : HeaderType_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	/**
	 * The column that the header cell represents.
	*/
	get column() : IgcColumnComponent {
	                                        const r = this.i.a;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = IgcColumnComponent._createFromInternal(r);
	                                            if (e) {
	                                                (e as any)._implementation = r;
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set column(v: IgcColumnComponent) {
		v == null ? this.i.a = null : this.i.a = v.i;
	}
	/**
	 * Indicates whether the column is currently sorted.
	*/
	get sorted() : boolean {
		return (this.i.g as boolean);
	}
	set sorted(v: boolean) {
		this.i.g = ensureBool(v);
	}
	/**
	 * Indicates whether the cell can be selected
	*/
	get selectable() : boolean {
		return (this.i.e as boolean);
	}
	set selectable(v: boolean) {
		this.i.e = ensureBool(v);
	}
	/**
	 * Indicates whether the cell is currently selected
	*/
	get selected() : boolean {
		return (this.i.f as boolean);
	}
	set selected(v: boolean) {
		this.i.f = ensureBool(v);
	}
	/**
	 * Indicates whether the column header is a title cell.
	*/
	get title() : boolean {
		return (this.i.h as boolean);
	}
	set title(v: boolean) {
		this.i.h = ensureBool(v);
	}
	/**
	 * Represents the sorting direction of the column (ascending, descending or none).
	*/
	get sortDirection() : SortingDirection {
		return this.i.b;
	}
	set sortDirection(v: SortingDirection) {
		this.i.b = ensureEnum<SortingDirection>(SortingDirection_$type, v);
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	if (this.column && (this.column as any).name && (this.column as any).name == name) { return this.column; }
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
