import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { GridActionsBaseDirective as GridActionsBaseDirective_internal } from "./GridActionsBaseDirective";
import { TypeRegistrar } from "igniteui-core/type";
import { fromSpinal, ensureBool } from "igniteui-core/componentUtil";

export class IgcGridActionsBaseDirective
{

protected createImplementation() : GridActionsBaseDirective_internal
{
	return new GridActionsBaseDirective_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : GridActionsBaseDirective_internal {
	return this._implementation;
	}/**
	                             * @hidden 
	                             */
	static _createFromInternal(internal: any) : IgcGridActionsBaseDirective {
	    if (!internal) {
	                                    return null;
	                                }
	                                let externalName = "";
	                                if (!internal.$type) {
	                                    if (internal.tagName && internal.tagName.startsWith("IGC-")) {
	                                        externalName = fromSpinal(internal.tagName.toLowerCase());
	                                        externalName = externalName.substr(0, 1).toUpperCase() + externalName.substr(1);
	                                    } else {
	                                        return null;
	                                    }
	                                } else {
	                                    externalName = "Igc" + internal.$type.name;
	                                }
	                                if (!TypeRegistrar.isRegistered(externalName)) {
	                                    return null;
	                                }
	                                return TypeRegistrar.create(externalName);
	                            }
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	get asMenuItems() : boolean {
		return (this.i.c as boolean);
	}
	set asMenuItems(v: boolean) {
		this.i.c = ensureBool(v);
	}
	get name() : string {
		return (this.i.f as string);
	}
	set name(v: string) {
		this.i.f = v;
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
